@import './../../global/global-style.scss';

.login-form-section-wrapper {
    background-color: $global-pages-background;
    width: 100%;

    .form-section {
        display: flex;
        justify-content: center;
        width: 100%;

        .container {
            width: 350px;
            @include min-cards-design-for-all-content(16px);
            margin: 2rem 0;
            @include display-flex-row-alignCenter(column);
            justify-content: center;
            min-height: 570px;

            .popup-content-signIn {


                .reset-password-form {
                    @include display-flex-row-alignCenter(column);
                    gap: 1.5rem;

                    input {
                        @include small-search-input();
                    }

                    .popup-buttons {
                        @include display-flex-row-alignCenter();
                        gap: 2rem;

                        .submit-action {
                            @include btn($dark-blue);
                            color: $white;
                            font-size: $font-size-18;
                            font-weight: $font-wight-700;
                            border-radius: 5px;

                            &:hover {
                                background-color: $hover-blue;
                            }

                        }

                        .close-action {
                            @include btn();
                            color: $white;
                            font-size: $font-size-18;
                            font-weight: $font-wight-700;
                            border-radius: 5px;

                            &:hover {
                                background-color: $hover-ruddy;
                            }
                        }
                    }
                }
            }

            .form-wrapper {
                @include display-flex-row-alignCenter(column);
                justify-content: center;
                gap: 1.5rem;
                width: 100%;

                .form-welcome {
                    font-size: $font-size-20;
                    font-weight: $font-wight-700;
                }

                .auth-buttons {
                    @include display-flex-row-alignCenter(column);
                    justify-content: center;
                    gap: .5rem;
                    width: 100%;

                    .google-login {
                        @include btn();
                        color: $white;
                        font-size: $font-size-18;
                        font-weight: $font-wight-700;
                        background-color: #C62828;
                        @include display-flex-row-alignCenter;
                        justify-content: center;
                        padding: 8px 0;
                        border-radius: 5px;
                        gap: .5rem;
                        width: 100%;
                    }

                    .facebook-login {
                        @include btn();
                        color: $white;
                        font-size: $font-size-18;
                        font-weight: $font-wight-700;
                        background-color: #0E64AA;
                        @include display-flex-row-alignCenter;
                        justify-content: center;
                        padding: 8px 0;
                        border-radius: 5px;
                        gap: .5rem;
                        width: 100%;
                    }
                }

                .or-wrapper {
                    @include display-flex-row-alignCenter;
                    justify-content: space-between;
                    width: 90%;

                    hr {
                        @include hr;
                        width: 45%;
                        margin: 0 auto;
                    }

                    .or {
                        width: 6%;
                    }
                }

                .form {
                    width: 100%;
                    @include display-flex-row-alignCenter(column);
                    justify-content: center;
                    gap: 1rem;

                    // success messages style
                    .errMessage {
                        @include btn;
                        width: 100%;
                        font-size: $font-size-18;
                        font-weight: $font-wight-700;
                        border-radius: 5px;
                        padding: 8px 0;
                        background-color: $danger;
                    }

                    .offScreen {
                        display: none
                    }

                    input[type=email] {
                        @include small-search-input;
                        font-size: $font-size-16;
                    }

                    input[type=password] {
                        @include small-search-input;
                        font-size: $font-size-16;
                    }

                    .remember-and-forget-pass {
                        width: 100%;
                        @include display-flex-row-alignCenter;
                        justify-content: space-between;

                        .check-box-wrapper {
                            @include display-flex-row-alignCenter;
                            justify-content: center;
                            gap: .3rem;
                        }

                        .forget-password {
                            color: $dark-blue;
                            cursor: pointer;
                        }
                    }

                    input[type=submit] {
                        @include btn;
                        width: 100%;
                        background-color: $dark-blue;
                        font-size: $font-size-18;
                        font-weight: $font-wight-700;
                        cursor: pointer;
                        border-radius: 5px;
                        padding: 8px 0;
                    }
                }

                hr {
                    @include hr;
                    width: 90%;
                    margin: 0 0;
                }

                .have-no-account-wrapper {
                    @include display-flex-row-alignCenter;
                    justify-content: center;
                    gap: .5rem;
                    font-size: $font-size-14;

                    .sign-up {
                        a {
                            cursor: pointer;
                            color: $dark-blue
                        }
                    }
                }
            }
        }
    }
}